@import "./reset.scss";

*,
* font font {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
}
body {
  background-color: #f8faff;
}

h2,
h2 font font {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 500;
  font-size: 64px;
  line-height: 80px;
  text-transform: uppercase;
}
h3,
h3 font font {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 50px;
  text-transform: uppercase;
}
h4,
h4 font font {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  text-transform: uppercase;
}
h5,
h5 font font {
  font-family: "Source Code Pro";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
}
.wrapper {
  max-width: 1171px;
  margin: 0 auto;
}

/*-------------------------------------------------------------------------*/
@import "./header.scss";
@import "./main.scss";
body {
  top: 0 !important;
}
.skiptranslate {
  display: none;
  height: 0;
}
.language__img {
  cursor: pointer;
}
#goog-gt-tt {
  display: none !important;
}
.library {
  margin-top: 122px;
  &__main {
    margin-top: 40px;
    padding: 40px 0 90px 40px;
    background-color: #fff;
    border-radius: 15px;
  }
}
.library-section {
  &__headline {
    text-transform: uppercase;
    margin-top: 20px;
    color: #a5b4c7;
    font-size: 20px;
    line-height: 25px;
  }
  &__link {
    font-size: 20px;
    line-height: 25px;
    margin-top: 10px;
    cursor: pointer;
  }
}
.active,
.active:visited {
  color: #0080ff;
}
.article {
  &__title {
  }
  &__main {
    margin-top: 40px;
    background-color: #fff;
    border-radius: 15px;
    padding: 40px;
  }
  &__title-img {
    display: block;
    margin: 0 auto;
  }
  &__img-block {
    margin-top: 20px;
  }
  &__img {
    display: block;
    margin: 0 auto;
  }
  &__img-description,
  &__img-description font font {
    text-align: center;
    font-weight: bold;
  }
}
.article-nav {
  display: flex;
  justify-content: space-between;

  &__link {
    color: #0080ff;
  }
  &_top {
    margin-top: 95px;
  }
  &_bottom {
    margin-top: 40px;
    padding-bottom: 105px;
  }
}
.section {
  &__title,
  &__subtitle {
    margin-top: 40px;
  }
  &__img {
    display: block;
    margin: 0 auto;
  }
  &__list {
    & li {
      margin-top: 3px;
      &::before {
        content: "\25A0";
        display: inline-block;
        position: relative;
        bottom: 3px;
        margin-right: 5px;
      }
    }
  }
  &__text,
  &__list {
    margin-top: 20px;
  }
}
.answer {
  display: block;
  margin-top: 10px;
  margin-left: 25px;
  padding: 15px 30px;
  border-radius: 10px;
  &_true {
    background-color: green;
  }
  &_false {
    background-color: red;
  }
}
.question {
  margin: 20px 0;
}
.language {
  cursor: pointer;
}
.language__menu {
  display: none;
  position: absolute;
}
.language__item:hover {
  color: #0080ff;
}
._active {
  display: block;
}
.article__path {
  color: #0c08ce;
  margin: 20px 0;
}

.library__giga {
  margin-top: 40px;
}
