.header {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
.header-menu {
  position: relative;
  transition: 0.5s;
  &__container {
    display: flex;
  }
  &__item {
    font-size: 20px;
    line-height: 25px;
    margin-right: 25px;
    cursor: pointer;

    &:hover {
      color: #0080ff;
      text-shadow: 0px 0px 4px #ebf5ff;
    }
  }
}
.burger {
  display: none; /* используем flex для центрирования содержимого */
  align-items: center; /* центрируем содержимое кнопки */
  position: absolute;
  right: 20px;
  top: 20px;
  width: 26px;
  height: 26px;
  cursor: pointer;
  z-index: 4;
}
.burger span,
.burger span::before,
.burger span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #616161;
  transition: 0.5s;
}
.burger span::before {
  content: "";
  top: -8px;
}
.burger span::after {
  content: "";
  top: 8px;
}

@media (max-width: 1200px) {
  .burger {
    display: flex;
  }
  .header-menu {
    position: fixed;
    z-index: 1;
    width: 300px;
    height: 100%;
    background-color: #a5b4c7;
    display: flex;
    align-items: center;
    justify-content: center;
    right: -100%;
	 top: 0;
  }
  .header-menu__container > a > li,
  .language,
  .navigation {
    margin-top: 20px;
  }
  .header {
	margin: 0;
	padding: 29px;
  }
  .header-menu__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    left: 0;
  }
}
@media (min-width: 576px) {
}
.cheked {
  right: 0%;
  top: 0;
  transition: 0.5s;
}
.lock {
  overflow: hidden;
}
.burger_cheked {
  transform: rotate(45deg);
  transition: 0.5s;
}
.burger_cheked > span::before {
  top: 0;
  transform: rotate(0);
  transition: 0.5s;
}
.burger_cheked > span::after {
  top: 0;
  transform: rotate(90deg);
  transition: 0.5s;
}
