.main {
  padding-bottom: 100px;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 122px;
  }

  &__headline {
    width: 476px;
    margin-bottom: 20px;
  }

  &__text {
    width: 476px;
    color: #0080ff;
  }
}
.logo {
  cursor: pointer;
}
.main-menu {
  margin-top: 69px;
  display: flex;

  &__item {
    background: #ffffff;
    border-radius: 15px;
    padding: 40px 20px 0 20px;
    margin: 30px 7px;
    cursor: pointer;
    &:hover {
      box-shadow: 0px 0px 5px 5px rgba(0, 128, 255, 0.5);
    }
  }

  &__img {
    margin-top: 60px;
  }
}
@media (max-width: 1200px) {
  .main-menu {
    flex-wrap: wrap;
    justify-content: center;
  }
  .main__header {
    flex-wrap: wrap;
    justify-content: center;
  }
}
